body {
  background: #DAE3E7;
}

.App {
  font-family: 'Nunito', sans-serif;
}

@media (min-width: 1400px) {
  .container {
    width: 1360px;
    max-width: inherit;
  }
}

.footer {
  position: fixed;
  bottom: 0;
  text-align: center;
  width: 100%;
  background: #32383e;
  color: #fff;
  padding: 10px 0;
}

.header .profile-content .social a {
  background: #b0b7bf;
  width: 36px;
  height: 36px;
  display: inline-block;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  border-radius: 50%;
  -moz-background-clip: padding;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  color: #fff;
  text-align: center;
}

.header .profile-content .social a .svg-inline--fa {
  font-size: 20px;
  margin-top: 8px;
}

.header {
  padding: 30px 0;
  background: #f5f5f5;
  border-top: 10px solid #778492;
}

.header .profile-image {
  margin-right: 30px;
  width: 15%;
  border-radius: 50%;
}

.header .profile-content .name {
  color: #49515a;
  font-size: 38px;
  margin-bottom: 5px;
  margin-top: 30px;
  font-weight: 700;
}

.header .profile-content .desc {
  color: #778492;
  font-weight: 400;
  font-size: 24px;
  margin-top: 0;
  margin-bottom: 15px;
  text-align: left;
}

.sections-wrapper {
  padding-top: 60px;
  padding-bottom: 60px;
}

.section {
  margin-bottom: 30px;
}

.section .section-inner {
  background: #fff;
  padding: 30px;
}

.section .heading {
  margin-top: 0;
  margin-bottom: 30px;
  color: #545e69;
  font-size: 24px;
  font-weight: 700;
}